import { Action } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export const TABLE_RESIZE_WIDTH_FIT_CONTAINER = Symbol('TABLE_RESIZE_WIDTH_FIT_CONTAINER');

export function resizeTableWidthFitContainer(table, containerWidth?: number) {
  return {
    type: TABLE_RESIZE_WIDTH_FIT_CONTAINER,
    payload: {
      node: table,
      containerWidth,
    },
  };
}

interface TableResizeTableWidthFitContainerPayload {
  node: Table;
  containerWidth?: number;
}

interface TableResizeTableWidthFitContainerAction {
  type: typeof TABLE_RESIZE_WIDTH_FIT_CONTAINER;
  payload: TableResizeTableWidthFitContainerPayload;
}

export function isTableResizeTableWidthFitContainerAction(
  action: Action,
): action is TableResizeTableWidthFitContainerAction {
  return action.type === TABLE_RESIZE_WIDTH_FIT_CONTAINER;
}